@import url('https://fonts.googleapis.com/css2?family=Jost:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Krona+One&display=swap');

:root {
    --background: rgb(255, 255, 255);
    --font: rgb(0, 0, 0);
    --font-white: rgb(255, 255, 255);
    --border: rgb(0, 0, 0);
    font-size: 20px;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Jost', sans-serif;
    font-weight: 400;
    background-color: var(--background);
    color: var(--font);
    line-height: 0;
}

/* Default */

h1 {
    font-family: 'Krona One', sans-serif;
    font-size: 3rem;
    font-weight: 500;
    margin: 0 0 30px 0;
    margin-bottom: 50px;
}

h2 {
    font-size: 0.75rem;
    font-weight: 400;
    font-family: 'Krona One', sans-serif;
}

h3 {
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
    margin: 0 0 20px 0;
    padding: 12px;
    margin-bottom: 42px;
}

a {
    color: #000;
    text-decoration: none;
}

button {
    margin: 0;
    padding: 0;
    border: none;
    background: none;
    font-family: 'Jost', sans-serif;
    font-weight: 400;
    font-size: 22px;
    color: var(--font);
    text-decoration: none;
    line-height: 1.6;
    cursor: pointer;
}

.visible {
    opacity: 1;
    z-index: 1;
    transition: opacity 0.05s linear;
}

.invisible {
    opacity: 0;
    z-index: 0;
    transition: opacity 0.05s linear, z-index 0.05s step-end;
}

.content-page {
    margin: 60px 60px 30px 60px;
    display: flex;
    flex-direction: column;
    line-height: 1.6;
}

.content-page h1 {
    margin-bottom: 30px;
    font-size: 2.5rem;
    line-height: 1.4;
    color: var(--font);
    
}

.content-page > * {
    margin: 10px 0;
    color: var(--font);
}

/* Navigation component */

.Navigation {
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0; 
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:white;
    z-index: 2;
}

.Navigation .links:first-child {
    margin-left: 60px;
}

.Navigation .links .link:not(:first-child) {
    margin-left: 40px;
}

.Navigation .link {
    text-decoration: none;
    line-height: 1.6;
    cursor: pointer;
}

.Navigation .home {
    margin-right: 60px;
    color: #000;
    text-transform: uppercase;
    font-size: 0.8rem;
    font-weight: 600;
    padding: 12px;
}

/* Map component */

.Map {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

#map-content {
    width: 100vw;
    height: 100vh;
}

.marker {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}

.PopUp {
    line-height: 1.4;
    padding: 50px 40px 60px 60px;
    border-radius: 0;
    font-family: 'Jost', sans-serif;
    font-size: 20px;
    position: absolute;
    z-index: 2;
    margin: 60px 60px 0 0;
    width: 25%;
}

.PopUp .snippet {
    margin: 10px 10px 10px 0;
}

.PopUp .link {
    font-weight:500;
    font-size: 16px;
    text-decoration: none;
    color: var(--font);
}

.PopUp .link:hover {
    text-decoration: underline;
    cursor: pointer;
}

/* Ideas page */

.Ideas .container {
    display: flex;
    flex-direction: column;
    margin-top: 60px;
}

/* IdeasRow component */

.IdeasRow {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 60px;
    line-height: 1.6;
}

.IdeasRow .tag {
    margin: 30px 0 0 0;
    padding: 12px;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--font);
    border: 3px solid var(--border);
}

.IdeasRow .title {
    margin: 30px 0 30px 0;
    font-family: 'Krona One', sans-serif;
    font-size: 2.5rem;
    color: var(--font);
    text-decoration: none;
    cursor: pointer;
}

/* Idea page */

.Idea .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--background);
}

.Idea header {
    box-sizing: border-box;
    width: 100%;
    padding: 120px 120px 30px 120px;  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    line-height: 1.4;
    background-color: #00000014;
}

.Idea header h3 {
    border: 3px solid #000;
}

.Idea .header-picture-container {
    width: 100%;
    padding-bottom: 100px;
    display: flex;
    justify-content: center;
    background-color: #00000014;
}

.Idea .header-picture-container img {
    width: 50%;
}

.Idea .container article {
    padding: 60px 12rem 0 12rem;
    line-height: 1.6;
    white-space: pre-line;
    max-width: 1600px;
}

.Idea .container article p {
    margin-bottom: 40px;
}

.Idea .container .author {
    margin-bottom: 60px;
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
}

.Idea .container .highlight-line {
    width: 120px;
    height: 24px;
    border-bottom: 3px solid #000;
    position: absolute;
}

.Idea .container .random-ideas {
    width: 100%;
    box-sizing: border-box;
}

/* Registry page */

.Registry .content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 60px;
    margin-top: 60px;
}

.Registry .content > * {
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
}

.Registry .content > *:not(:last-child) {
    border-bottom: 3px solid #000;
}

.Registry .content .word {
    margin-right: 30px;
}

.Registry .content .link {
    margin-right: 20px;
}

.Registry .content .dot {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
}

/* About page, PrivacyPolicy page, Imprint page, Registry page */

.About, .PrivacyPolicy, .Imprint, .Registry {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #fff;
}

/* Footer component */

footer {
    height: 60px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 60px 0 0;
}

footer span {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

footer span a:last-child {
    margin-left: 30px;
}

.Discover footer {
    position: absolute;
    bottom: 0;
    right: 0;
}

.Discover footer Link {
    padding: 0;

}

/* Media queries */

/* Tablet - max-width: 1080px */

@media only screen and (max-width: 811px) {

    :root {
        font-size: 16px;
    }

    /* .IdeasRow .title, h1 {
        font-size: 38px;
    } */





}

@media only screen and (max-width: 376px) {

    :root {
        font-size: 10px;
    }




}















